* {
  padding: 0;
  margin: 0;
  font-family: "Roboto", sans-serif;
}

.Header-Container {
  display: flex;
  justify-content: center;
  background-color: #ffcd9e;
  height: 600px;
}

.Left-Header {
  width: 50%;
  padding: 2rem;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
}

.header {
  height: 70vh;
  max-width: 1200px;
  display: flex;
  flex-direction: row;
}

.Left-Header h1 {
  position: absolute;
  width: 562px;
  height: 108px;
  left: 136px;
  top: 83px;

  font-family: "Inter";
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 150%;
  /* or 54px */

  color: #282828;
}

.Left-Header p {
  position: absolute;
  width: 495px;
  height: 81px;
  left: 136px;
  top: 219px;

  font-family: "Inter";
  font-style: normal;
  font-weight: 500;
  font-size: 20px;
  line-height: 160%;
  /* or 32px */

  display: flex;
  align-items: center;

  color: #484848;
}

.Left-Header b {
  position: absolute;
  width: 227px;
  height: 81px;
  left: 136px;
  top: 315px;

  font-family: "Inter";
  font-style: normal;
  font-weight: 800;
  font-size: 20px;
  line-height: 160%;
  /* or 32px */

  display: flex;
  align-items: center;

  color: #282828;
}

.Right-Header {
  width: 50%;
}

