  position: absolute;
  width: 350px;
  height: 61px;
  left: 129px;
  top: 411px;
  border: 2px solid #282828;
  border-radius: 5px;

  font-family: "Inter";
  font-style: normal;
  font-weight: 600;
  font-size: 20px;
  line-height: 160%;
  /* or 32px */

  display: flex;
  align-items: center;

  color: #888888;
}
